<template>
  <v-card class="pl-8 pr-8 pt-8">
    <template>
      <mrc_ess_mytree :list='treeList'
                      @getData='getData'
                      :disabled='disabled'
                      :isload='isload'
                      :uuids='uuidslist'
                      ref='reftree'></mrc_ess_mytree>
    </template>
  </v-card>
</template>    

<script>  
import { updatequanxian, getTreeList } from '@/service/api/clientmanage_api.js'

export default {
  components: {
  },
  props: {
    uuidslist: {  //用户选中的按钮
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    oldtree: [],  //老数据 用户一开始的数据
    function_permission: [//功能权限 用户实时选中的数据   
    ],
    delete_function_buttons: [], //删除数据
    uuids: [],
    isload: false,
    treeList: [] //完整的树
  }),
  watch: {
  },
  methods: {
    getchangeref () {
      return this.$refs.reftree.getCheckData()
    },
    getData (v) {  //获取最新的选择列表
      // console.log(v);
      this.function_permission = v
    },
    typedelmethods (oldarr, newarr, comparisonvalue) { //加typedel方法
      var result = []  //
      for (var i = 0; i < oldarr.length; i++) {
        var c_value = oldarr[i].comparisonvalue
        var isExist = false
        for (let j = 0; j < newarr.length; j++) {
          if (newarr[j].comparisonvalue == c_value) {
            isExist = true
            break
          }
        }
        if (!isExist) {
          result.push({ ...oldarr[i], '~type~': 'del' })
        }
      }
      return result
    },
    async gettreelist () { //获取默认tree树  
      const listtree = await getTreeList()
      this.treeList = listtree.data
    },
  },
  async mounted () {
    await this.gettreelist()
    this.isload = false
    this.isload = true
  }

}
</script>

<style scoped>
